import { make } from 'vuex-pathify'

const userProfile = window.localStorage.profile

const state = {
  drawer: null,
  drawerImage: true,
  mini: true,
  items: [],
}

const homePage = {
  title: 'Home',
  icon: 'mdi-home',
  to: '/home/',
}
const financialPage = {
  title: 'Financeiro',
  icon: 'mdi-cash',
  to: '/staff/financial/',
}
const financialReport = {
  title: 'Relatório de Lançamentos',
  icon: 'mdi-clipboard-text',
  to: '/staff/reports/financial/',
}
const fleetControl = {
  title: 'Controle de Frota',
  icon: 'mdi-truck',
  to: '/empresa/frota/',
}
const userProfilePage = {
  title: 'Perfil de Usuário',
  icon: 'mdi-account',
  to: '/profile/',
}
const companyProfilePage = {
  title: 'Perfil da Empresa',
  icon: 'mdi-factory',
  to: '/perfil/',
}
const testsPage = {
  title: 'Testes',
  icon: 'mdi-test-tube',
  to: '/tests/',
}

const itemsMaster = [
  homePage,
  financialPage,
  financialReport,
  fleetControl,
  userProfilePage,
  companyProfilePage,
  testsPage,
]
const itemsAdmin = [
  homePage,
  financialPage,
  financialReport,
  userProfilePage,
]
const itemsFinancial = [
  homePage,
  financialPage,
  userProfilePage,
]
const itemsCustomer = [
  homePage,
  fleetControl,
  userProfilePage,
  companyProfilePage,
]

if (userProfile === 'MASTER') {
  state.items = itemsMaster
} else if (userProfile === 'FINANCIAL') {
  state.items = itemsFinancial
} else if (userProfile === 'ADMIN') {
  state.items = itemsAdmin
} else if (userProfile === 'CUSTOMER') {
  state.items = itemsCustomer
} else {
  state.items = []
}

const mutations = make.mutations(state)

// const actions = {
//   ...make.actions(state),
//   init: async ({ dispatch }) => {
//     //
//   },
// }

const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  // actions,
  getters,
}
