import Vue from 'vue'
import Router from 'vue-router'
import { trailingSlash } from '@/util/helpers'
import {
  layout,
  route,
} from '@/util/routes'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: (to, from, savedPosition) => {
    if (to.hash) return { selector: to.hash }
    if (savedPosition) return savedPosition

    return { x: 0, y: 0 }
  },
  routes: [
    layout('Default', [
      route('Login Screen'),

      // Pages
      route('Financial Control', null, 'staff/financial'),
      route('Financial Report', null, 'staff/reports/financial'),
      route('Fleet Control', null, 'empresa/frota'),
      route('Home View', null, '/home'),
      route('Tests View', null, '/tests'),

      // Components
      route('User Profile', null, '/profile'),
      route('Company Profile', null, '/perfil'),

      // Images
      route('manualusuario', null, '/assets/manualusuario'),
    ]),
  ],
})

router.beforeEach((to, from, next) => {
  return to.path.endsWith('/') ? next() : next(trailingSlash(to.path))
})

export default router
